        "description": "Yi Yike, who seems to have a perfect life, lives through her 30s thinking it is as it should be. Whether in the company or at home, she is thoughtful all around. Like a never-tired Wonder Woman, she takes care of everyone around her, but herself. Until she met in a corner of a convenience store, the boy who often went to eat at her home when she was a child, but now a man full of sunshine, Xu Guangxi. Xu Guangxi meets Yike again after returning from studying abroad, and his sense of dependency from his dependence has become a fluttering heartbeat, but chasing Yike is not an easy task for Guangxi, and it means to endure the fast-pace and overdrive of pressure. But because of love, in the long years, Guangxi has become more and more mature; readjusting the pace, and moving forward firmly. The two throbbing hearts who have experienced countless challenges and choices are getting closer little by little, and they now also understand that the best love is not to sacrifice each other, but to attract and respect each other, so that both can become better people.",
