        "description": "Zhao Pan'er, who opened a teahouse in Hangzhou, finally received the good news that her fiance Ouyang Xu was ranked third in national civil examination, but was abandoned. Unwilling to accept her fate, she vowed to go to Bianjing, the Capital, to seek justice. On her way there, she had to rush to rescue her good sister, Song Yinzhang, who was cheated on marriage and was abused. At the same time, she also saved Sun Sanniang, who had unfortunately tried to commit suicide because of her miserable married life. When Ouyang Xu learned that Zhao Pan'er had come to the Capital, he tried many ways to drive her out. Zhao Paner, Song Yinzhang, and Sun Sanniang decided to stay in Bianjing, relying on their own ability to make a fortune. After experiencing difficulties and various tribulations, the three sisters finally managed the small teahouse into the largest restaurant in Bianjing. After an arduous struggle, Zhao Pan'er gained more insights, and also erased her hatred towards Ouyang Xu, and at the same time contributed a lot to the salvation and equality for the humble women in ancient times.",
